  .control-panel {
    font-family: 'Orbitron';
    font-weight: 100;
    font-size: 14px;
    width: 400px;
    margin: 5em auto;
    -webkit-transform: rotate3d(0, 1, 0, 25deg);
    transform: rotate3d(0, 1, 0, 25deg);
    color: #0ff;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    -webkit-animation: rotate3d 15s infinite ease-in-out;
    animation: rotate3d 15s infinite ease-in-out;
  }
  .control-panel-monitor,
  .control-panel-controls {
    padding: 1em;
    width: 400px;
    height: 200px;
    border: 1px solid #fff;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    background: rgba(0,255,255,0.05);
    -webkit-box-shadow: 0 0 5px 1px #0ff, 0 0 5px 1px #0ff inset;
            box-shadow: 0 0 5px 1px #0ff, 0 0 5px 1px #0ff inset;
    position: relative;
  }
  .control-panel-monitor:before,
  .control-panel-controls:before,
  .control-panel-monitor:after,
  .control-panel-controls:after {
    content: '';
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,255,255,0.05);
    border: 1px solid;
    -webkit-box-shadow: 0 0 5px 1px #0ff, 0 0 5px 1px #0ff inset;
            box-shadow: 0 0 5px 1px #0ff, 0 0 5px 1px #0ff inset;
  }
  .control-panel-monitor:before,
  .control-panel-controls:before {
    left: -2px;
    color: #f00;
  }
  .control-panel-monitor:after,
  .control-panel-controls:after {
    left: 2px;
    color: #00f;
  }
  .control-panel-monitor:before {
    clip: rect(23px, 400px, 10px, 0);
    -webkit-animation: glitch-1 100ms infinite linear alternate-reverse;
    animation: glitch-1 100ms infinite linear alternate-reverse;
  }
  .control-panel-monitor:after {
    clip: rect(72px, 400px, 23px, 0);
    -webkit-animation: glitch-2 100ms infinite linear alternate-reverse;
    animation: glitch-2 100ms infinite linear alternate-reverse;
  }
  .control-panel-controls {
    -webkit-transform: rotateX(30deg) translateZ(50px);
    transform: rotateX(30deg) translateZ(50px);
  }
  .control-panel-controls:before {
    clip: rect(29px, 400px, 6px, 0);
    -webkit-animation: glitch-3 100ms infinite linear alternate-reverse;
    animation: glitch-3 100ms infinite linear alternate-reverse;
  }
  .control-panel-controls:after {
    clip: rect(83px, 400px, 4px, 0);
    -webkit-animation: glitch-4 100ms infinite linear alternate-reverse;
    animation: glitch-4 100ms infinite linear alternate-reverse;
  }
